import Vue from 'vue'
import Router from 'vue-router'

Vue.use(Router)

export default new Router({
  mode: 'history', // 访问路径不带井号  需要使用 history模式，才能使用 scrollBehavior  
  routes: [
    {
      path: '/',
      component: resolve => require(['@/pages/home/Index'], resolve),  // 使用懒加载
      meta: {
        keepAlive: true  // true 表示需要使用缓存
      }      
    },
    {
      path: '/getOneProduct/:id',
      component: resolve => require(['@/pages/details/Index'], resolve),  // 使用懒加载
      meta: {
        keepAlive: false  // false 表示不需要使用缓存
      }         
    },    
    {
      path: '/getProductCategory/:id',
      component: resolve => require(['@/pages/classification/Index'], resolve),  // 使用懒加载
      meta: {
        keepAlive: false  // false 表示不需要使用缓存
      }         
    },        
    {
      path: "*",
      redirect: "/"
    }           
  ],
  scrollBehavior(to, from, savedPosition) {
    if (savedPosition) {
        return savedPosition
    } else {
        return {
            x: 0,
            y: 0
        }
    }
  }    
})



