<template>
  <div id="app">
    <keep-alive>
      <router-view v-if="$route.meta.keepAlive">
        <!-- 这里是会被缓存的视图组件 -->
      </router-view>
    </keep-alive>

    <router-view v-if="!$route.meta.keepAlive">
      <!-- 这里是不被缓存的视图组件 -->
    </router-view>

    <!-- 页脚 -->
    <div class="footer">
        <p>广州来钱宝软件服务有限公司 
            <a href="http://www.miitbeian.gov.cn/publish/query/indexFirst.action" >粤ICP备18007183号-3</a>
        </p>           
    </div>    
  </div>
</template>

<script>
export default {
  name: 'App'
}
</script>

<style lang="scss" scoped>
  .footer{
      text-align: center;
      margin: 16px 0px;

      p{
        font-size:10px;
        font-family:MicrosoftYaHei;
        font-weight:400;
        color:rgba(204,202,202,1);
        line-height:39px;
      }
      a{
          color: rgba(204,202,202,1);
      }
  }
</style>



